import EOLCoordinateSet from './EOLCoordinateSet'
import EOLLineSegment from './EOLLineSegment'

export default class EOLObject {
  public start_row: number
  public end_row: number
  public end_column: number

  public attributes: string[]
  public coordinates: EOLCoordinateSet[]
  public hasConflict: boolean = false
  public conflictRanges: { line: number, start: number, end: number }[] = []

  constructor(row: number) {
    this.start_row = row
    this.end_row = row
    this.end_column = 0
    this.attributes = []
    this.coordinates = []
  }

  public resetConflicts() {
    this.conflictRanges = []
  }

  public buildFile(): string[] {
    if (this.coordinates.length <= 0) return []
    
    const output: string[] = []
    output.push('#ObjectBegin')
    this.attributes.forEach(key => {
      output.push(key)
    })
    output.push('#CoordinatesBegin')
    for (const coord of this.coordinates) {
      output.push(`#CoordinateSet ${coord.point.x.toFixed(3)} ${coord.point.y.toFixed(3)} ${coord.data.join(' ')}`)
    }
    output.push('#CoordinatesEnd')
    output.push('#ObjectEnd')

    return output
  }

  public get lineSegments(): EOLLineSegment[] {
    if (this.coordinates.length <= 1) return []
    const result: EOLLineSegment[] = []
    for (let i = 0; i < this.coordinates.length - 1; i++) {
      const coord1 = this.coordinates[i]
      const coord2 = this.coordinates[i + 1]
      result.push(new EOLLineSegment(coord1.point, coord2.point))
    }

    return result
  }

  public get isPPoint(): boolean {
    return this.coordinates.filter(p => p.isPPoint) !== undefined
  }

  /**
   * Compares the two objects for conflicts
   * @param obj2 Object to compare
   * @returns 0 if there are no conflicts, 1 if this is the conflicting item with the least amount of segments, -1 otherwise
   */
  public isConflicting(obj2: EOLObject): 0 | -1 | 1 {
    const list1 = this.lineSegments
    const list2 = obj2.lineSegments

    for (let i = 0; i < Math.min(list1.length, list2.length); i++) {
      const ls1 = list1[i]
      const ls2 = list2[i]
      if (ls1.equals(ls2)) {
        return list1.length > list2.length ? -1 : 1
      }
    }

    list1.reverse()
    list2.reverse()
    for (let i = 0; i < Math.min(list1.length, list2.length); i++) {
      const ls1 = list1[i]
      const ls2 = list2[i]
      if (ls1.equals(ls2)) {
        return list1.length > list2.length ? -1 : 1
      }
    }

    return 0
  }

  public processConflict(obj2: EOLObject) {
    const list1 = this.lineSegments
    const list2 = obj2.lineSegments

    for (let i = 0; i < Math.min(list1.length, list2.length); i++) {
      const ls1 = list1[i]
      const ls2 = list2[i]
      if (ls1.equals(ls2)) {
        this.conflictRanges.push({
          line: ls1.start.row,
          start: ls1.start.col_start,
          end: ls1.start.col_end
        })

        this.conflictRanges.push({
          line: ls1.end.row,
          start: ls1.end.col_start,
          end: ls1.end.col_end
        })

        this.conflictRanges.push({
          line: ls2.start.row,
          start: ls2.start.col_start,
          end: ls2.start.col_end
        })

        this.conflictRanges.push({
          line: ls2.end.row,
          start: ls2.end.col_start,
          end: ls2.end.col_end
        })
      }
    }

    console.log(this.conflictRanges)

  }

  public getConflict(obj2: EOLObject): string | undefined {
    let equality = this.coordinateCompare(this.coordinates, obj2.coordinates)
    if (equality !== undefined) {
      return `Duplicate coordinates found: ${equality.map(e => `${e.x} ${e.y}`).join('/')}`
    }
    
    if (this.coordinates.length > obj2.coordinates.length) {
      equality = this.coordinateCompare(this.coordinates.reverse(), obj2.coordinates)
      if (equality !== undefined) {
        return `Duplicate coordinates found: ${equality.map(e => `${e.x} ${e.y}`).join('/')}`
      }
    }
    else {
      equality = this.coordinateCompare(this.coordinates, obj2.coordinates.reverse())
      if (equality !== undefined) {
        return `Duplicate coordinates found: ${equality.map(e => `${e.x} ${e.y}`).join('/')}`
      }
    }

    return undefined
  }

  public outwardTrace(
    startingPoint: EOLObject | undefined,
    passedPoints: EOLObject[],
    itemsMap: Map<[number, number], EOLObject[]>,
    incomingCoordinate: [number, number]) {
    if (startingPoint !== undefined) {
      passedPoints.push(startingPoint)
    }

    if (this.endPoint && this.endPoint.point.equals(incomingCoordinate)) {
      this.coordinates.reverse()
    }

    if (this.endPoint) {
      const nextKey: [number, number] = [this.endPoint.point.x, this.endPoint.point.y]
      const nextCoord = itemsMap.get(nextKey) ?? []
      for (const next of nextCoord.filter(i => !passedPoints.find(a => a === i))) {
        next.outwardTrace(this, passedPoints, itemsMap, nextKey)
      }
    }
  }

  private coordinateCompare(coord1: EOLCoordinateSet[], coord2: EOLCoordinateSet[]) {
    const result: { x: number, y: number, row1: number, row2: number }[] = []
    for (let i = 0; i < Math.min(coord1.length, coord2.length); i++) {
      const c1 = coord1[i]
      const c2 = coord2[i]
      result.push({ x: c1.point.x, y: c1.point.y, row1: c1.point.row, row2: c2.point.row })
      if (!c1.equals(c2)) {
        return undefined
      }
    }

    return result
  }

  public get startPoint(): EOLCoordinateSet | undefined {
    return this.coordinates[0]
  }

  public get endPoint(): EOLCoordinateSet | undefined {
    return this.coordinates.slice(-1)[0]
  }

}