import EOLCoordinateSet from './EOLCoordinateSet'
import EOLObject from './EOLObject'
import EOLPoint from './EOLPoint'

export default class EOLFile {
  public header: string[]
  public objects: EOLObject[]

  constructor() {
    this.header = []
    this.objects = []
  }

  public buildFile(): string[] {
    const output: string[] = []
    output.push(...this.header)

    for (const obj of this.objects) {
      output.push(...obj.buildFile())
    }

    return output
  }

  public Process() {
    for (let i = 0; i < this.objects.length - 1; i++) {
      const obj1 = this.objects[i]
      for (let j = i + 1; j < this.objects.length; j++) {
        const obj2 = this.objects[j]
        let breakout = false
        switch (obj1.isConflicting(obj2)) {
          case -1:
            this.objects.splice(j, 1)
            j--
            continue
          case 1:
            this.objects.splice(i, 1)
            i--
            breakout = true
            break
        }
        if (breakout) {
          break
        }
      }
    }
    this.OrganizeData()
  }

  private OrganizeData() {
    const map = new Map<[number, number], EOLObject[]>()

    for (const obj of this.objects) {
      if (obj.startPoint === undefined || obj.endPoint === undefined || obj.startPoint === obj.endPoint) continue
      
      const lst_start: EOLObject[] = map.get(obj.startPoint.asTuple) ?? []
      lst_start.push(obj)
      map.set(obj.startPoint.asTuple, lst_start)

      const lst_end: EOLObject[] = map.get(obj.endPoint.asTuple) ?? []
      lst_end.push(obj)
      map.set(obj.endPoint.asTuple, lst_start)
    }

    const start: [number, number, boolean][] = []
    map.forEach((val, key) => {
      if (val.length === 1) {
        start.push([...key, val[0].isPPoint])
      }
    })

    start.sort((a, b) => a[2]? b[2]? 0: -1: 1)

    const passed: EOLObject[] = []
    for (const key of start) {
      const obj = map.get([key[0], key[1]])
      if (obj && obj[0]) {
        obj[0].outwardTrace(undefined, passed, map, [key[0], key[1]])
      }
    }
  }
 
  public static Parse(data: string): EOLFile {
    const lines = data.split('\n').map(line => line.trim())
    if (lines.length <= 0) {
      throw new Error('Data is empty')
    }
      
    if (!lines[0].startsWith('#EOL')) {
      throw new Error(`Expected line 1 to start with #EOL, got ${lines[0]}`)
    }

    const result = new EOLFile()
    let header_ended = false
    for (let i = 0; i < lines.length; i++) {
      if (lines[i] === '#ObjectBegin') {
        header_ended = true
      }

      if (!header_ended) {
        result.header.push(lines[i])
        continue
      }

      if (lines[i] === '#ObjectBegin') {
        const res = EOLFile.ReadObject(lines, i)
        if (res !== undefined) {
          i = res.index
          result.objects.push(res.obj)
        }
      }
    }
    return result
  }

  private static ReadObject(data: string[], index: number): {index: number, obj: EOLObject} | undefined {
    if (data[index] !== '#ObjectBegin') return undefined

    const result = new EOLObject(index + 1)

    while (index < data.length && data[index] !== '#ObjectEnd') {
      if (data[index].startsWith('#CoordinateSet')) {
        const items = data[index].split(' ')
        const x = parseFloat(items[1])
        const y = parseFloat(items[2])
        const row = index + 1
        
        const col_start = data[index].indexOf(items[1]) + 1
        const col_end = data[index].indexOf(items[2]) + 1 + items[1].length + 1

        const coord = new EOLCoordinateSet(new EOLPoint(x, y, row, col_start, col_end), items.slice(3))
        result.coordinates.push(coord)
      }
      else if (data[index] !== '#ObjectBegin' && data[index] !== '#CoordinatesEnd' && data[index] !== '#ObjectEnd' && data[index] !== '#CoordinatesBegin') {
        result.attributes.push(data[index])
      }
      index++
    }
    result.end_row = index + 1
    result.end_column = data[index].trim().length
    return { index: index, obj: result }
  }
}