import EOLPoint from './EOLPoint'

export default class EOLCoordinateSet {
  public point: EOLPoint
  public data: string[]
  constructor(point: EOLPoint, data: string[] = []) {
    this.point = point
    this.data = data
  }

  public get isPPoint(): boolean {
    if (this.data && this.data.length) {
      return this.data[this.data.length - 1].trim().startsWith('P')
    }
    return false
  }

  public get asTuple(): [number, number] {
    return this.point.asTuple
  }

  public equals(c2: EOLCoordinateSet) {
    return this.point.equals(c2.point)
  }
}