import { useEffect, useRef, useState } from 'react'
import { Col, Row, Container } from 'react-bootstrap'
import EOLFile from '../classes/EOLFile'
import {Monaco} from '@monaco-editor/react'
import * as monaco from 'monaco-editor/esm/vs/editor/editor.api';
import './MainComponent.scss'
import { useDebounce } from 'usehooks-ts'
import { DiffEditor } from '@monaco-editor/react'
import DxfParser from 'dxf-parser'
import EOLObject from '../classes/EOLObject'
import EOLCoordinateSet from '../classes/EOLCoordinateSet'
import EOLPoint from '../classes/EOLPoint'

const MainComponent = () => {
  const [value, setValue] = useState('')
  const debouncedValue = useDebounce(value, 500)

  const [result, setResult] = useState('')
  const editorRef = useRef<monaco.editor.IDiffEditor | null>(null);
  
  useEffect(() => {
    analyze()
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [debouncedValue])

  const handleEditorDidMount = (editor: monaco.editor.IDiffEditor, monaco: Monaco) => {
    editorRef.current = editor
    editorRef.current.getContainerDomNode().ondrop = function(ev: DragEvent) {
      if (ev.dataTransfer && ev.dataTransfer.files && ev.dataTransfer.files.length) {
        ev.preventDefault()
        ev.stopPropagation()
        readFileToValue(ev.dataTransfer.files[0])
      }
    }
    monaco.languages.register({ id: 'eol' })
    monaco.languages.registerFoldingRangeProvider('eol', {
      provideFoldingRanges: function (model: monaco.editor.ITextModel, context: monaco.languages.FoldingContext, token: monaco.CancellationToken) {
        const ranges: monaco.languages.FoldingRange[] = []

        const lines = model.getLinesContent()
        let rangeStart = -1
        let currentLine = 1

        for (const line of lines) {
          if (rangeStart === -1 && line.startsWith('#ObjectBegin')) {
            rangeStart = currentLine
          }

          if (!line.trim()) {
            currentLine++
            continue
          }

          if (line.startsWith('#ObjectEnd') && rangeStart !== -1) {
            const foldingRange = {
              start: rangeStart,
              end: currentLine,
              kind: {value: 'object'}
            }
            ranges.push(foldingRange)
            rangeStart = -1
          }

          currentLine++
        }

        return ranges
      }
    })
  }

  const parseDxf = async(data: string, filename: string) => {
    const parser = new DxfParser()
    try {
      const dxf = await parser.parse(data)
      if (!dxf) {
        return
      }
      let line = 1
      const result = new EOLFile()
      result.header.push('#EOL=1.1')
      result.header.push(`#MapFileName="${filename}"`)
      line++
      for (const entity of dxf.entities) {
        if ('vertices' in entity && Array.isArray(entity.vertices)) {
          if (entity.type === 'LINE' || entity.type === 'LWPOLYLINE' || entity.type === 'POLYLINE') {
            const obj = new EOLObject(line++)
            obj.attributes.push(`#GroupName="${entity.layer.toLowerCase()}"`)
            const verts = entity.vertices
            for (const vert of verts) {
              obj.coordinates.push(new EOLCoordinateSet(new EOLPoint(vert.x, vert.y, line++, 1, 1)))
            }
            result.objects.push(obj)
          }
          else {
            console.log(entity.type)
          }
        }
      }

      setValue(result.buildFile().join('\n'))
    }
    catch {}
  }

  const analyze = () => {
    if (value !== '') {
      try {
        const obj = EOLFile.Parse(value)
        obj.Process()
        setResult(obj.buildFile().join('\n'))
      }
      catch (e) {
        if (e instanceof Error) {
          setResult(`Error in input: ${e.message}`)
          console.log(e.stack)
        }
      }
    }
    else {
      setResult('')
    }
  }

  const readFileToValue = (file: File) => {
    const fn = file.name.toLowerCase().trim()
    const fileType = fn.endsWith('.dxf') ? 'dxf' : fn.endsWith('.eol') ? 'eol' : undefined

    const fs = new FileReader()
      fs.onload = function () {
        if (typeof fs.result === 'string' && fileType) {
          if (fileType === 'eol') {
            setValue(fs.result)
          }
          else if (fileType === 'dxf') {
            parseDxf(fs.result, fn)
          }
        }
      }
      fs.readAsText(file)
  }

  return <Container fluid>
    <Row>
      <Col><h2>Result</h2></Col>
      <Col><h2>Original</h2></Col>
    </Row>
    <Row>
      <Col>
        <DiffEditor
          original={result}
          modified={value}
          options={{ glyphMargin: true }}
          onMount={handleEditorDidMount}
          language='eol'
          height='90vh'
        />
      </Col>
    </Row>
  </Container>
}

export default MainComponent