export default class EOLPoint {
  public x: number
  public y: number
  public row: number
  public col_start: number
  public col_end: number

  constructor(x: number, y: number, row: number, col_start: number, col_end: number) {
    this.x = x
    this.y = y
    this.row = row
    this.col_start = col_start
    this.col_end = col_end
  }

  public get asTuple(): [number, number] {
    return [this.x, this.y]
  }

  public equals(point: EOLPoint | [number, number]): boolean {
    if (Array.isArray(point)) {
      return this.x === point[0] && this.y === point[1]
    }
    else {
      return this.x === point.x && this.y === point.y
    }
  }
}