import EOLPoint from './EOLPoint'

export default class EOLLineSegment {
  public start: EOLPoint
  public end: EOLPoint

  constructor(start: EOLPoint, end: EOLPoint) {
    this.start = start
    this.end = end
  }

  public equals(seg2: EOLLineSegment) {
    return (this.start.equals(seg2.start) && this.end.equals(seg2.end)) ||
        (this.start.equals(seg2.end) && this.end.equals(seg2.start))
  }
}